<template>
  <div class="flex flex-col h-screen max-h-screen">

    <div class="z-10 flex justify-center realtive bg-[#094183] bg-unimelb-logo bg-no-repeat pl-8 pr-8 px-4 pt-4 pb-24">
      <!-- lga info -->
      <div class="absolute
        flex
        flex-col
        lg:flex-row
        bg-white
        gap-y-8
        gap-x-8
        px-4
        pt-4
        pb-3
        rounded-md"
      >
        <!-- flex item #1 -->
        <div class="flex flex-col">
            <h3 class="text-xs mb-1 uppercase">LGA Name</h3>
            <span class="text-xl" id="lganame"></span>
        </div>
        <!-- flex item #2 -->
        <div class="flex flex-col">
            <h3 class="text-xs mb-1 uppercase">Positive Sentiment</h3>
            <span class="text-xl" id="positive_count"></span>
        </div>
        <!-- flex item #2 -->
        <div class="flex flex-col">
            <h3 class="text-xs mb-1 uppercase">Negative Sentiment</h3>
            <span class="text-xl" id="negative_count"></span>
        </div>
        <!-- flex item #3 -->
        <div class="flex flex-col">
            <h3 class="text-xs mb-1 uppercase">Tweet Total Number</h3>
            <span class="text-xl" id="total_count"></span>
        </div>
      </div>
    </div>

    <!-- Map -->
    <div id="mapid" class="h-full z-10">
    </div>
  </div>
</template>

<script>
import leaflet from "leaflet"
import polygonJs from "../assets/map.json"

export default {
  name: 'HomeView',
  data() {
    return {
    }
  },
  mounted() {
    // base map and set to Melbourne View
    var mymap = leaflet.map('mapid').setView([-37.9536, 145.2631], 9);

    // map layers
    var whiteLayer = leaflet.tileLayer('https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png', {
      maxZoom: 25,
      attribution: '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors'
    }).addTo(mymap)
    var darkLayer = leaflet.tileLayer('https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png', {
      maxZoom: 20,
      attribution: '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors'
    });
    var satLayer = leaflet.tileLayer('http://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}', {
      maxZoom: 20,
      subdomains: ['mt0', 'mt1', 'mt2', 'mt3']
    });
    var baseMaps = {
      "White Layer": whiteLayer,
      "Dark Layer": darkLayer,
      "Satellite Layer": satLayer,
    };
    
    // event that highlight the area and show info on header
    function highlightFeature(e) {
      var layer = e.target;

      layer.setStyle({
        weight: 4,
        color: "#666",
        dashArray: "",
        fillOpacity: 0.7
      })

      // show info on header
      document.getElementById("lganame").innerHTML = layer.feature.properties.vic_lga__3;
      document.getElementById("positive_count").innerHTML = layer.feature.properties.senti_pos_count;
      document.getElementById("negative_count").innerHTML = layer.feature.properties.senti_neg_count;
      document.getElementById("total_count").innerHTML = layer.feature.properties.total_tweets;
      console.log(layer.feature.properties);
    }

    // event that reset highlight for negative layer
    function resetNegHighlight(e){
      negativeLayer.resetStyle(e.target);
    }

    // event that reset highlight for positive layer
    function resetPosHighlight(e){
      positiveLayer.resetStyle(e.target);
    }

    // event that reset highlight for positive layer
    function resetRentHighlight(e){
      rentLayer.resetStyle(e.target);
    }

    function resetIncomeHighlight(e){
      incomeLayer.resetStyle(e.target);
    }


    // color map
    function getPosColor(d) {
      return  d > 0.50  ? '#800026' :
              d > 0.47  ? '#BD0026' :
              d > 0.43  ? '#E31A1C' :
              d > 0.40  ? '#FC4E2A' :
              d > 0.35  ? '#FD8D3C' :
              d > 0.30  ? '#FEB24C' :
              d > 0.25  ? '#FED976' :
                          '#FFEDA0';
    }

    function getNegColor(d) {
      return  d > 0.40  ? '#800026' :
              d > 0.37  ? '#BD0026' :
              d > 0.33  ? '#E31A1C' :
              d > 0.30  ? '#FC4E2A' :
              d > 0.25  ? '#FD8D3C' :
              d > 0.20  ? '#FEB24C' :
              d > 0.15  ? '#FED976' :
                          '#FFEDA0';
    }

    function getRentColor(d) {
      return  d > 500  ? '#800026' :
              d > 460  ? '#BD0026' :
              d > 420  ? '#E31A1C' :
              d > 400  ? '#FC4E2A' :
              d > 380  ? '#FD8D3C' :
              d > 360  ? '#FEB24C' :
              d > 340  ? '#FED976' :
                          '#FFEDA0';
    }

    function getIncomeColor(d) {
      return  d > 7000  ? '#800026' :
              d > 6000  ? '#BD0026' :
              d > 5000  ? '#E31A1C' :
              d > 4000  ? '#FC4E2A' :
              d > 3000  ? '#FD8D3C' :
              d > 2000  ? '#FEB24C' :
              d > 1000  ? '#FED976' :
                          '#FFEDA0';
    }




    // style function for negative layer
    function styleNeg(feature) {
        return {
            fillColor: getNegColor(feature.properties.senti_neg_dens),
            weight: 2,
            opacity: 1,
            color: 'white',
            dashArray: '3',
            fillOpacity: 0.4
        };
    }

    // style function for positive layer
    function stylePos(feature) {
        return {
            fillColor: getPosColor(feature.properties.senti_pos_dens),
            weight: 2,
            opacity: 1,
            color: 'white',
            dashArray: '3',
            fillOpacity: 0.4
        };
    }

    function styleRent(feature) {
        return {
            fillColor: getRentColor(feature.properties.rent_median),
            weight: 2,
            opacity: 1,
            color: 'white',
            dashArray: '3',
            fillOpacity: 0.4
        };
    }

    function styleIncome(feature) {
        return {
            fillColor: getIncomeColor(feature.properties.low_income_card),
            weight: 2,
            opacity: 1,
            color: 'white',
            dashArray: '3',
            fillOpacity: 0.4
        };
    }


    // deal with event and show the popup message
    function onNegEachFeature(feature, layer) {
      layer.on({
          mouseover: highlightFeature,
          mouseout: resetNegHighlight,
          click: layer.bindPopup(
            "<ol><li><b>Local Government Area:          </b>" + feature.properties.vic_lga__3 + 
            "</li><li><b>Positive Tweet Count: </b>" + feature.properties.senti_pos_count +
            "</li><li><b>Negative Tweet Count:          </b>" + feature.properties.senti_neg_count +
            "</li><li><b>Positive Tweet Density:        </b>" + feature.properties.senti_pos_dens +
            "</li><li><b>Negative Tweet Density:        </b>" + feature.properties.senti_neg_dens +
            "</li><li><b>Tweet Total Number:            </b>" + feature.properties.total_tweets + 
            "</li><li><b>Rent Median:                   </b>" + feature.properties.rent_median + 
            "</li><li><b>Lower Income Number:           </b>" + feature.properties.low_income_card + 
            "</li></ol>"
          )
        }
      )
    }

    function onPosEachFeature(feature, layer) {
      layer.on({
          mouseover: highlightFeature,
          mouseout: resetPosHighlight,
          click: layer.bindPopup(
            "<ol><li><b>Local Government Area:          </b>" + feature.properties.vic_lga__3 + 
            "</li><li><b>Positive Tweet Count: </b>" + feature.properties.senti_pos_count +
            "</li><li><b>Negative Tweet Count:          </b>" + feature.properties.senti_neg_count +
            "</li><li><b>Positive Tweet Density:        </b>" + feature.properties.senti_pos_dens +
            "</li><li><b>Negative Tweet Density:        </b>" + feature.properties.senti_neg_dens +
            "</li><li><b>Tweet Total Number:            </b>" + feature.properties.total_tweets + 
            "</li><li><b>Rent Median:                   </b>" + feature.properties.rent_median + 
            "</li><li><b>Lower Income Number:           </b>" + feature.properties.low_income_card + 
            "</li></ol>"
          )
        }
      )
    }

    function onRentEachFeature(feature, layer) {
      layer.on({
          mouseover: highlightFeature,
          mouseout: resetRentHighlight,
          click: layer.bindPopup(
            "<ol><li><b>Local Government Area:          </b>" + feature.properties.vic_lga__3 + 
            "</li><li><b>Positive Tweet Count: </b>" + feature.properties.senti_pos_count +
            "</li><li><b>Negative Tweet Count:          </b>" + feature.properties.senti_neg_count +
            "</li><li><b>Positive Tweet Density:        </b>" + feature.properties.senti_pos_dens +
            "</li><li><b>Negative Tweet Density:        </b>" + feature.properties.senti_neg_dens +
            "</li><li><b>Tweet Total Number:            </b>" + feature.properties.total_tweets + 
            "</li><li><b>Rent Median:                   </b>" + feature.properties.rent_median + 
            "</li><li><b>Lower Income Number:           </b>" + feature.properties.low_income_card + 
            "</li></ol>"
          )
        }
      )
    }

    function onIncomeEachFeature(feature, layer) {
      layer.on({
          mouseover: highlightFeature,
          mouseout: resetIncomeHighlight,
          click: layer.bindPopup(
            "<ol><li><b>Local Government Area:          </b>" + feature.properties.vic_lga__3 + 
            "</li><li><b>Positive Tweet Count: </b>" + feature.properties.senti_pos_count +
            "</li><li><b>Negative Tweet Count:          </b>" + feature.properties.senti_neg_count +
            "</li><li><b>Positive Tweet Density:        </b>" + feature.properties.senti_pos_dens +
            "</li><li><b>Negative Tweet Density:        </b>" + feature.properties.senti_neg_dens +
            "</li><li><b>Tweet Total Number:            </b>" + feature.properties.total_tweets + 
            "</li><li><b>Rent Median:                   </b>" + feature.properties.rent_median + 
            "</li><li><b>Lower Income Number:           </b>" + feature.properties.low_income_card + 
            "</li></ol>"
          )
        }
      )
    }

    var negativeLayer = leaflet.geoJSON(polygonJs, {
      onEachFeature: onNegEachFeature, 
      style: styleNeg
    }).addTo(mymap);

    var positiveLayer = leaflet.geoJSON(polygonJs, {
      onEachFeature: onPosEachFeature,
      style: stylePos
    })

    var rentLayer = leaflet.geoJSON(polygonJs, {
      onEachFeature: onRentEachFeature,
      style: styleRent
    })

    var incomeLayer = leaflet.geoJSON(polygonJs, {
      onEachFeature: onIncomeEachFeature,
      style: styleIncome
    })

    // var legend = leaflet.control({position: 'bottomright'});
    // legend.onAdd = function () {
    //   var div = leaflet.DomUtil.create('div', 'info legend bg-white rounded-md pl-4 pr-4 px-2 py-2 flex-col');
    //   var categories = [0.40, 0.37, 0.33, 0.30, 0.25, 0.20, 0.15, 0];

    // for (var i = 0; i < categories.length; i++) {
    //   div.innerHTML += '<font color="' + getColor(categories[i] + 0.01) + '"> > ' + parseFloat(categories[i]).toFixed(2) + "</font><br>";
    //   //div.innerHTML += ii + categories[i] + '</i><br>';
    // }
    // return div;
    // };
    // legend.addTo(mymap);

    var overlayMaps = {
      "Negative Sentiment": negativeLayer,
      "Positive Sentiment": positiveLayer,
      "Median Rent": rentLayer,
      "Lower income layer": incomeLayer
    };

    var layerControler = leaflet.control.layers(baseMaps, overlayMaps, {
      collapsed: false
    })
    layerControler.addTo(mymap);
  }
}
</script>
